@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,300;0,400;0,600;1,300;1,400;1,600&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{margin:0;padding:0;font-family:'Josefin Sans';font-size:16px}.section{width:100vw;height:100vh;display:flex;justify-content:center;align-items:center}.col-content{height:610px;display:flex;flex-direction:column;justify-content:space-around;margin-left:200px;margin-right:200px}.left{background-image:url(/base-apparel-coming-soon-master/static/media/bg-pattern-desktop.964ebd4d.svg);background-repeat:no-repeat;background-size:cover}.col{height:810px;display:flex;align-items:center}h2{font-weight:600;font-size:4em;text-transform:uppercase;letter-spacing:20px}h2>strong{font-size:1em;color:#ce9797}p{line-height:35px;color:#ce9797}form{width:100%}.input-with-button>form{display:flex;border-radius:30px;width:100%}.input{width:100%;border-radius:30px;padding-left:25px;padding-top:5px;border:2px solid #f96262;opacity:0.5;background-color:transparent}.input::after{width:45px;height:45px;content:' ';background-image:url(/base-apparel-coming-soon-master/static/media/icon-error.77009af7.svg);margin-left:-100px}input[type=submit]{cursor:pointer;width:90px;height:45px;border-radius:30px;margin-left:-50px;border:none;z-index:100;background:url(/base-apparel-coming-soon-master/static/media/icon-arrow.4b3d2d93.svg),linear-gradient(135deg, #f8bfbf 0%, #ee8c8c 100%);background-position:center;background-repeat:no-repeat}.input:focus{outline:none !important;border:2px solid #f96262}.content-error{height:50px;display:flex;align-items:center}.error{color:#ce9797;font-weight:bold;padding-left:25px}@media (min-width: 320px) and (max-width: 992px){.section{width:100%;height:100%;flex-direction:column-reverse}.right{display:flex;flex-direction:column;align-items:flex-start}.right>.image>img{content:url(/base-apparel-coming-soon-master/static/media/hero-mobile.1afd055b.jpg)}.right::before{margin-top:30px;margin-left:30px;margin-bottom:30px;content:url(/base-apparel-coming-soon-master/static/media/logo.bfe35add.svg)}.col-content{padding:30px}.col-content>.image>img{display:none}.col,.col-content{width:auto;height:auto;margin:0}input[type=submit]{width:150px}h2{margin-top:30px;text-align:center;font-size:2.5em;letter-spacing:15px}p{margin-top:25px;margin-bottom:25px;line-height:25px;text-align:center}.error{width:100%;text-align:center}}

